// Core variables and mixins
@import "../dev/variables.less";


.spa-layout {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 10px;
    right: 0;
    border-radius: 5px;
    overflow: hidden;
    padding: 2%;
}

.spa-view {
    position: relative;
    top: 0;
    left: 0;
    display: none;
}

.spa-current-view {
    display: block;
    overflow: hidden;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}

.spa-child-view {
    height: 100%;
    position: relative;
}

/*
    addedd this to override the bootstrap defaults
*/
a:focus,
a:hover,
a:active {
    text-decoration: none;
}

@media(min-width:975px) {

    .spa-current-view {
        min-height: 300px;
    }
}
